import { SVGPathDataParser } from 'svg-pathdata';
import Draggable from '../dependencies/draggable';
import { Stage } from '..';
class Spline {
  #stage: Stage;
  #isDragging: Boolean = false;
  #splineHud?: SVGElement;
  #grippiesGroup?: SVGGElement;
  #bezierHud?: SVGElement;
  #bezierG?: SVGElement;
  #bezierP?: SVGPathElement;
  #bezierL1?: SVGLineElement;
  #bezierL2?: SVGLineElement;
  #bezierC1?: SVGCircleElement;
  #bezierC2?: SVGCircleElement;
  #splines = new Map();
  #isDrawing: Boolean = false;
  #checkedGrippie?: SVGElement;
  #startX?: number;
  #startY?: number;
  #isDisabled: Boolean = true;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
    this.#initGrippies();
  }
  #install = () => {
    this.#stage.stage?.addEventListener('mousedown', this.#mousedown);
    document.addEventListener('mousemove', this.#mousemove);
    document.addEventListener('mouseup', this.#mouseup);

    this.#splineHud = document.querySelector('#spline-hud') as SVGElement;
    this.#grippiesGroup = this.#splineHud?.querySelector(
      "[uid='primary-node-grippies']",
    ) as SVGGElement;
    this.#bezierHud = document.querySelector('#cubic-bezier-seg-hud') as SVGElement;
    this.#bezierG = this.#bezierHud.querySelector("[uid='container']") as SVGElement;
    this.#bezierP = this.#bezierHud?.querySelector("[uid='outline']") as SVGPathElement;
    this.#bezierL1 = this.#bezierHud.querySelector("[uid='control-line-1']") as SVGLineElement;
    this.#bezierL2 = this.#bezierHud.querySelector("[uid='control-line-2']") as SVGLineElement;
    this.#bezierC1 = this.#bezierHud.querySelector(
      "[uid='control-point-1-grippie']",
    ) as SVGCircleElement;
    this.#bezierC2 = this.#bezierHud.querySelector(
      "[uid='control-point-2-grippie']",
    ) as SVGCircleElement;
  };
  #initGrippies = () => {
    const { a, e, f } = this.#stage.matrix;
    const parser = new SVGPathDataParser();
    this.#stage.select?.get().forEach((item: any) => {
      const node = item.node as any;
      if (node.localName !== 'path') return;
      const d = node.getAttribute('d').replaceAll(',', '');
      const m = node.getCTM();
      const pathData = parser.matrix(m.a, m.b, m.c, m.d, m.e, m.f).parse(d);
      pathData.forEach((dItem: any, index: number) => {
        if (dItem.type === 1) return;
        const { x, y } = dItem;
        let type = 'mid';
        if (d.toLowerCase().includes('z') || d.toLowerCase().includes('m')) {
          if (dItem.type === 2 && !d.toLowerCase().includes('z')) {
            type = 'start';
          } else if (index === pathData.length - 1) {
            type = 'end';
          }
          this.#createGrippies((x - e) / a, (y - f) / a, type, node, index);
        } else {
          this.#createGrippies((x - e) / a, (y - f) / a, type, node, index);
        }
      });
    });
  };
  #mousedown = (ev: MouseEvent) => {
    if (ev.button !== 0 || this.#isDisabled) return;

    console.log(this.#isDisabled);
    ev.stopPropagation();
    this.#isDragging = true;
    (this.#bezierG as any).style.visibility = 'visible';
    const { offsetX, offsetY, clientX, clientY } = ev;
    const m = this.#stage.svgRoot?.getMatrixIterator();
    const x = (offsetX - m.e) / m.a;
    const y = (offsetY - m.f) / m.d;
    console.log('mousedown');
    // this.#startX = clientX;
    // this.#startY = clientY;

    if (!this.#isDrawing) {
      this.#isDrawing = true;
      this.#startX = x;
      this.#startY = y;
    } else {
      if (this.#startX && this.#startY) {
        const node = this.#createSvgNode('path');
        node.setAttribute('style', `fill:rgb(216, 216, 216);stroke:rgb(0, 0, 0)`);
        this.#stage.svgRoot?.append(node);
        this.#stage.select?.set(node, { node });
        this.#createGrippies(this.#startX, this.#startY, 'start', node, 1);
        node.setAttribute('d', `M ${this.#startX} ${this.#startY}`);
      }

      const node = this.#splines.get(this.#checkedGrippie).node;
      const d = node.getAttribute('d');
      node.setAttribute('d', `${d} L ${x} ${y}`);
      this.#createGrippies(x, y, 'end', node, 1);

      const preGrippie = this.#checkedGrippie?.previousSibling as Element;
      if (preGrippie.getAttribute('data-position') === 'end') {
        preGrippie?.setAttribute('data-position', 'mid');
        preGrippie?.querySelector('.inner-circle')?.remove();
      }

      this.#startX = void 0;
      this.#startY = void 0;
    }

    this.#bezierC1?.setAttribute('cx', x.toFixed(9));
    this.#bezierC1?.setAttribute('cy', y.toFixed(9));
    this.#bezierP?.setAttribute('d', `M ${x} ${y} `);
  };
  #mousemove = (ev: MouseEvent) => {
    if (getComputedStyle(this.#bezierG as Element).visibility === 'hidden') return;
    const { clientX, clientY } = ev;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const m = this.#stage.svgRoot?.getMatrixIterator();
    const x = (clientX - left - m.e) / m.a;
    const y = (clientY - top - m.f) / m.d;
    if (!this.#isDragging) {
      const bfX = this.#bezierC1?.getAttribute('cx');
      const bfY = this.#bezierC1?.getAttribute('cy');
      this.#bezierP?.setAttribute('d', `M ${bfX} ${bfY} L ${x} ${y}`);
      this.#bezierC2?.setAttribute('cx', x.toFixed(9));
      this.#bezierC2?.setAttribute('cy', y.toFixed(9));
    } else {
    }
  };
  #mouseup = () => {
    this.#isDragging = false;
  };
  #createGrippies = (
    x: number,
    y: number,
    pos: string,
    node: Element,
    index: number,
    selected = false,
  ) => {
    this.#unSelect();
    const g = this.#createSvgNode('g') as SVGGElement;
    g.classList.add('primary-node-grippie');
    g?.setAttribute('data-position', pos);
    if (selected) g.toggleAttribute('data-selected');
    const outCir = this.#createSvgNode('circle') as SVGCircleElement;
    outCir.classList.add('outer-circle');

    outCir.setAttribute('cx', x.toFixed(9));
    outCir.setAttribute('cy', y.toFixed(9));
    outCir.setAttribute('r', '5');
    g.append(outCir);
    if (pos !== 'mid') {
      const innerCir = this.#createSvgNode('circle') as SVGCircleElement;
      innerCir.classList.add('inner-circle');
      innerCir.setAttribute('cx', x.toFixed(9));
      innerCir.setAttribute('cy', y.toFixed(9));
      innerCir.setAttribute('r', '1.5');
      g.append(innerCir);
    }
    this.#splines.set(g, {
      node,
      index,
      pathData: '',
    });
    this.#grippiesGroup?.append(g);
    this.#checkedGrippie = g;
    new Draggable().install({
      element: g,
      start: () => {
        const { node, index } = this.#splines.get(g);
        const parser = new SVGPathDataParser();
        const d = node.getAttribute('d').replaceAll(',', '');
        const pathData = parser.parse(d);
        this.#splines.set(g, {
          node,
          index,
          pathData,
        });
        if (d.toLowerCase().includes('z')) {
          g.toggleAttribute('data-selected');
        }
      },
      move: (e: Draggable) => {
        const { dataX, dataY } = e;
        const { node, index, pathData } = this.#splines.get(g);
        const pathArr = JSON.parse(JSON.stringify(pathData));

        const d_value = pathArr.map((item: any, i: number) => {
          if (index === i) {
            item.x += dataX;
            item.y += dataY;
            g.childNodes.forEach((childNode: any) => {
              if (childNode.nodeType === 3) return;
              childNode.setAttribute('cx', item.x.toFixed(9));
              childNode.setAttribute('cy', item.y.toFixed(9));
            });
          }
          return `${this.#typeParse(item.type)} ${item.x || ''} ${item.y || ''} `;
        });
        node.setAttribute('d', d_value);
      },
      end: () => {
        this.#unSelect();
        if (g.dataset.position === 'start') {
          const node = this.#splines.get(this.#checkedGrippie).node;
          const d = node.getAttribute('d');
          node.setAttribute('d', d + ' Z');
          this.closeBezierG();
          this.#isDrawing = false;
        } else {
          //   if (!g.hasAttribute("data-selected"))
          //     g.toggleAttribute("data-selected");
        }
      },
    });
    return g;
  };
  #unSelect = () => {
    const selectedArr = document.querySelectorAll('[data-selected]');
    selectedArr.forEach((node: Element) => {
      node.removeAttribute('data-selected');
    });
  };
  #createSvgNode(type: string): SVGElement {
    return document.createElementNS('http://www.w3.org/2000/svg', type);
  }
  closeBezierG = () => {
    this.#bezierG?.setAttribute('style', 'visibility:hidden');
    this.#bezierC1?.setAttribute('cx', '-9999999');
    this.#bezierC1?.setAttribute('cy', '-9999999');
    this.#bezierC2?.setAttribute('cx', '-9999999');
    this.#bezierC2?.setAttribute('cy', '-9999999');
  };
  #typeParse = (type: number) => {
    const typeObj = { 1: 'Z', 2: 'M', 16: 'L', 32: 'C' };
    return (typeObj as any)[type];
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
  uninstall = () => {
    this.#stage.stage?.removeEventListener('mousedown', this.#mousedown);
    document.removeEventListener('mousemove', this.#mousemove);
    document.removeEventListener('mouseup', this.#mouseup);
    this.closeBezierG();
    if (this.#grippiesGroup) this.#grippiesGroup.innerHTML = '';
    this.#splines.clear();
    this.#isDrawing = false;
    this.#startX = void 0;
    this.#startY = void 0;
  };
}

export default Spline;
